
<script>
import weui from 'weui.js'
export default {
  name: 'vui-picker',
  props: {
    items: {
      type: Array,
      required: true
    },
    show: {
      type: Boolean,
      default: false
    },
    value: Array,
    // 
    className: String,
    // pickeriddefaultValueid
    id: {
      type: String,
      default: () => {
        return new Date().getTime() + ''
      }
    },
    container: String,
    // picker(picker) 1-3items
    depth: Number
  },
  mounted() {
    if (this.show) {
      this.showPicker()
    }
  },
  watch: {
    show () {
      if (this.show) {
        this.showPicker()
      }
    }
  },
  methods: {
    /**
     * picker
     */
    showPicker () {
      const _this = this
      const pickerOption = {
        id: this.id,
        /**
         * picker
         */
        onChange: function (result) {
          _this.$emit('change', result)
        },
        /**
         * ""(Array)picker
         * @param result
         */
        onConfirm: function (result) {
          _this.$emit('confirm', result.map(item => {
            return item.value
          }))
          // v-model
          _this.$emit('input', result.map(item => {
            return item.value
          }))
        },
        onClose: function (result) {
          // 
          _this.$emit('update:show', false)
          _this.$emit('close', result)
        }
      }
      // 
      if (this.value) {
        pickerOption.defaultValue = this.value
      }
      if (this.className) {
        pickerOption.className = this.className
      }
      if (this.container) {
        pickerOption.container = this.container
      }
      if (this.depth) {
        pickerOption.depth = this.depth
      }
      weui.picker(...this.items, pickerOption)
    }
  },
  /* eslint-disable */
  render(h) {
    return null
  }
}
</script>

